import {
    REFRESH_ING,
    LOADING_ING,
    GET_ALL_TRENDS,
    LOAD_TRENDS,
    TEST,
    GET_LIKE,
    GET_COMMENT,
    TOGGLE_COMMENT,
    CHANGE_COMMENT,
    ERROR,
    CLEAR_ALL,
    SHOW_CAROUSEL,
    HIDE_CAROUSEL,
    CHANGE_QUAN,
    CHANGE_QUAN_CHENGYUAN,
} from '../actions/ActionsTypes'

import {ListView} from 'antd-mobile'

const initState = {
    dataSource: new ListView.DataSource({
        rowHasChanged: (row1, row2) => row1 !== row2,
    }),
    refreshing: false,
    loading: false,
    //网络请求状态
    pageSize : 1,                  // 当前页
    size : 20,                  // 请求条目数
    maxPage: 1,              // 总页数
    total:0,                 // 总个数
    canLoad: false,
    showComment: false,
    commentStr: '',
    selectedItem: undefined,
    rows:[
        // {
        //     name:"df",
        //     content: "受到广泛大概地方刚刚获得的返回房间规划局是德国和国家的风景都是电风扇广泛化工23fffddfgdfdgfdghgf",
        //     type: 1,
        //     thumbsups: [
        //         {
        //             name: "djy"
        //         },
        //         {
        //             name: "djy2"
        //         },
        //         {
        //             name: "djy3"
        //         }
        //     ],
        //     comment: [
        //         {
        //             name: "djy",
        //             contect: "对方的广泛大锅饭",
        //         },
        //         {
        //             name: "djyerew",
        //             contect: "对方的广泛大锅饭",
        //         },
        //
        //     ],
        // },
    ],

    showCarousel: false,
    imgArr: [],
    imgIndex: 0,
    quan: {},
    usercount: 0,
    }

export default function dongtai(state = initState, action) {
    switch (action.type) {
        case CLEAR_ALL:
            return Object.assign({}, state, {rows : []});
            break;
        case ERROR:
            return Object.assign({}, state, {refreshing : false, loading:false});
            break;
        case REFRESH_ING:
            return Object.assign({}, state, {refreshing : true});
            break;
        case LOADING_ING:
            return Object.assign({}, state, {loading : true});
            break;
        case TOGGLE_COMMENT:
            return Object.assign({}, state, {
                showComment : !state.showComment,
                selectedItem: action.res.selectedItem,
            });
            break;
        case CHANGE_COMMENT:
            return Object.assign({}, state, {commentStr : action.res.commentStr});
            break;
        case CHANGE_QUAN:
            return Object.assign({}, state, {
                quan: action.res.quan,
                usercount: action.res.quan.usercount,
            });
            break;
        case CHANGE_QUAN_CHENGYUAN:
            console.log("djy---CHANGE_QUAN_CHENGYUAN---" + action.res.usercount);
            return Object.assign({}, state, {
                usercount: action.res.usercount,
            });
            break;
        case GET_ALL_TRENDS:
            const {total, rows} = action.res;
            let maxPage = (total == 0 ? 1 : Math.ceil(total/state.size));
            console.log('GET_ALL_TRENDS canLoad-->'+(maxPage>1));
            return Object.assign({}, state, {
                refreshing : false,
                maxPage: maxPage,
                pageSize: 1,
                canLoad: maxPage>1,
                rows: rows,
                total: total,
            });
            break;

        case LOAD_TRENDS:
            let newRows = state.rows.concat(action.res.rows);
            let canLoad = action.res.pageSize < state.maxPage;
            console.log('LOAD_TRENDS canLoad-->'+canLoad);

            return Object.assign({}, state, {
                pageSize: action.res.pageSize,
                canLoad: canLoad,
                rows: newRows,
                loading : false,
            });
            break;

        case GET_LIKE:
            return Object.assign({}, state, {
                rows: state.rows.map(row =>
                    row.trendsid === action.res.trendsid ?
                        Object.assign({}, row, { thumbsups: action.res.thumbsups }) :
                        row
                )
            });
            break;

        case GET_COMMENT:
            return Object.assign({}, state, {
                showComment: false,
                commentStr: "",
                rows: state.rows.map(row =>
                    row.trendsid === action.res.trendsid ?
                        Object.assign({}, row, { comment: action.res.comment }) :
                        row
                )
            });
            break;

        case TEST:
            return Object.assign({}, state, {
                rows: state.rows.concat(state.rows),
                refreshing : false,
            });
            break;
        default:
            return state;

        case SHOW_CAROUSEL:
            return Object.assign({}, state, {
                showCarousel : true,
                imgArr: action.res.imgArr,
                imgIndex: action.res.imgIndex,
            });
            break;
        case HIDE_CAROUSEL:
            return Object.assign({}, state, {
                showCarousel : false,
                imgArr: [],
            });
            break;
    }
}


