/**
 * 时间戳转时间
 * @param timestr
 * @returns {string}
 */
export function getLocalTime(timestr) {
    if(timestr){
        let timeChuoInt = Number(timestr);
        let newDate = new Date();
        newDate.setTime(timeChuoInt * 1000);
        return newDate.toLocaleString();
    }
    return "";
}
/**
 * 时间戳距离现在多久时间
 * @param timestr
 * @returns {string}
 */
export function getAgoTime(timestr) {
    if(timestr){
        let diff = new Date().getTime()/1000 - Number(timestr);
        let day_diff = Math.floor(diff/86400); //一天

        return (day_diff == 0 && (
            diff < 60 && "刚刚" ||
            diff < 120 && "1分钟前" ||
            diff < 3600 && Math.floor(diff/60)+"分钟前" ||
            diff < 7200 &&"1小时前" ||
            diff < 86400 && Math.floor(diff/3600)+"小时前"))||
            day_diff == 1 && "昨天" ||
            day_diff < 7 && day_diff+"天前" ||
            Math.ceil(day_diff/7) + "周前";
    }
    return "";
}


